@import '../../styles/utils/variables';

.event {
  max-width: 300px;
  padding: 15px;
  border: 1px solid gray;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  position: relative;
  transition: all $transitionDuration;

  &__edit {
    position: absolute;
    right: 15px;
    display: block;
    width: 40px;
    height: 40px;
    background: center / 24px no-repeat url("../../assets/icons/edit.svg"), #ffffffbc;

    &:hover {
      border: 1px solid #000;
    }
  }

  &__img {
    display: block;
    width: 100%;
    height: 300px;
    object-fit: cover;
    margin-bottom: 18px;
  }

  &__no-poster {
    height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: darkblue;
    font-size: 32px;
    font-weight: 500;
    color: #fff;
    margin-bottom: 18px;
  }

  &__title {
    font-weight: 700;
    text-transform: capitalize;
    margin-bottom: 20px;
    line-height: 150%;
    flex: 1 1 auto;

    a {
      color: inherit;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  &__info {
    display: flex;
    justify-content: space-between;
  }

  &:hover {
    box-shadow: 1px 2px 9px 0 #8a8a8a;
  }
}